.about-require-ads-two {
    @include adaptiveValue("border-top-left-radius", 88, 24, 1296, 576, 1);
    @include adaptiveValue("border-top-right-radius", 88, 24, 1296, 576, 1);
    @include adaptiveValue("padding-top", 108, 46, 1296, 576, 1);
    background-color: var(--white);
    position: relative;

    .container {
        @media (max-width:$smallTablet-max) {
            padding: 0;
        }
    }

    &__top {
        margin: 0 auto;
        max-width: 815px;

        @media (max-width:$smallTablet-max) {
            padding: 0 16px;
        }
    }

    &__wrapper {
        margin-top: 68px;
        background: #000;
        color: var(--white);
        @include adaptiveValue("border-radius", 70, 24, 1296, 576, 1);
        padding: 70px 84px 84px;
        position: relative;
        overflow: hidden;
        z-index: 2;

        &::before {
            content: '';
            background-image: url('../img/gradient-block.png');
            background-size: cover;
            background-repeat: no-repeat;
            background-position: 50% 50%;
            position: absolute;
            inset: 0;
            width: 100%;
            height: 100%;
            z-index: -1;
        }

        &:nth-child(2) {
            display: flex;
            flex-direction: column;

            .row {
                margin-top: 98px;
                display: grid;
                grid-template-columns: repeat(3, 1fr);
                grid-column-gap: 24px;
            }

            .card-info-secondary__descr {
                max-width: 261px;
            }
        }

        &:nth-child(3) {
            margin-top: 24px;
        }

        @media (max-width:$tablet-max) {
            margin-top: 32px;
            border-radius: 32px;
            padding: 45px 32px;
            flex-direction: column;
            justify-content: start;
        }

        @media (max-width:$smallTablet-max) {
            padding: 32px 16px;
            border-radius: 18px;
        }
    }

    &__descr {
        font: var(--subtitle-1);
        max-width: 561px;

        span {
            color: #89B2EF;
        }
    }

    &__audio {
        display: flex;
        justify-content: space-between;

        .about-require-ads-two__descr {
            max-width: 307px;
        }

        .custom-audio {

            &__wrapper {}

            &__tracks {
                margin-top: 40px;

                @media (max-width:$tablet-max) {
                    margin-top: 24px;
                }
            }

            &__track {
                display: flex;
                align-items: center;
                position: relative;
                z-index: 1;
                svg {
                    width: 21px;
                    height: 21px;
                    fill: none;
                    stroke: rgba(#fff, 0.5);
                    stroke-width: 1.5px;
                    transition: stroke $transitionSpeed $transitionFunction;
                }

                span {
                    margin-left: 10px;
                    font: var(--text-1);
                    color: rgba(#fff, 0.5);
                    transition: color $transitionSpeed $transitionFunction;
                }

                &:not(:last-child) {
                    margin-bottom: 16px;
                }

                &::before {
                    content: '';

                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%) translateX(-50%);
                    left: 0;
                    width: 65px;
                    height: 65px;
                    background-image: url('../img/blur-6.svg');
                    background-repeat: no-repeat;
                    background-size: cover;

                    opacity: 0;
                    visibility: hidden;
                    z-index: -1;
                    transition: opacity $transitionSpeed $transitionFunction,
                        visibility $transitionSpeed $transitionFunction;

                        pointer-events: none;
                }

                &._active {
                    svg {
                        stroke: #fff;

                    }

                    span {
                        color: #fff;
                    }

                    &::before {
                        opacity: 1;
                        visibility: visible;
                    }
                }

            }
        }
    }

    @media (max-width:$tablet-max) {
        margin-top: 56px;
    }
}
